body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

.title {
  justify-content: center;
  display: flex;
}

.title h1 {
  color: orange;
  margin: 0 0 1.875rem;
  font-size: 2rem;
  line-height: 1.5;
}

.sudoku {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: .625rem;
  display: flex;
}

.sudoku > .board {
  margin-left: .625rem;
  margin-right: .625rem;
  display: inline-block;
}

.sudoku > .board .board-row {
  gap: .625rem;
  display: flex;
}

.sudoku > .board .board-row:not(:last-child) {
  margin-bottom: .625rem;
}

.sudoku > .board .board-row .box {
  border: 1px solid #000;
  display: inline-block;
}

.sudoku > .board .board-row .box .box-row {
  display: flex;
}

.tile {
  box-sizing: border-box;
  cursor: pointer;
  color: #333;
  background-color: #f9fafb;
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 1.5rem;
  display: flex;
}

.tile.invalid {
  color: #b91c1c;
  background-color: #ff4d4d33;
}

.tile.initial {
  color: #1e3a8a;
  cursor: auto;
  background-color: #e0e7ff;
}

.tile.invalid.initial {
  color: #1e3a8a;
  cursor: auto;
  background-color: #ff4d4d33;
}

.tile.active {
  background-color: #bee7c8;
  border-width: 2px;
  border-color: #3b82f6;
}
